$(document).ready(function () {

	$('.mdb-select').materialSelect();

	/* Format Phone */
	$('.phone').bind('keypress change blur', function () {
		format_phone(this);
	}).attr('maxlength', '14');

	function format_phone(obj) {
		var numbers = obj.value.replace(/\D/g, ''),
			char = {
				0: '(',
				3: ') ',
				6: '-'
			};
		obj.value = '';
		for (var i = 0; i < numbers.length; i++) {
			if (i > 13) {
				return false;
			}
			obj.value += (char[i] || '') + numbers[i];

		}
	}

	var d = new Date();
	var year = d.getFullYear();
	var month = ('0' + (d.getMonth())).slice(-2);
	var day = ('0' + (d.getDate())).slice(-2);
	$('.datepicker').pickadate({
		format: 'yyyy-mm-dd',
		min: [year, month, day]
	});
	$('.datepicker').removeAttr('readonly');

	$('.time').pickatime({
		twelvehour: true,
		default: 'now',
		autoclose: true,
	});

	var _token = $('meta[name="csrf-token"]').attr('content');


	/* login and auth */
	$.ajaxSetup({
		headers: {
			'X-CSRF-TOKEN': _token
		}
	});

	/* had to add this function to this file because it would not work in global.js */
	function register_user() {

		var email = $('#register_email').val();
		var password = $('#register_password').val();
		var phone = $('#register_phone').val();
		var name = $('#register_name').val();

		$.ajax({
			type: 'post',
			url: '/register/user',
			data: {
				_token: _token,
				email: email,
				password: password,
				phone: phone,
				name: name
			},
			success: function (data) {
				if (data.status == 'error') {
					$('#register_error').show();
					$('#already_registered_login').unbind('click').bind('click', function () {
						$('#modalRegisterForm').modal('hide');
						$('#modalSignInForm').modal();
					});
				} else {
					$('#modalRegisterForm').modal('hide');
					$('#nav_logged_in').html('<div id="nav_logged_in"><a href="/dashboard" class="mb-n2  float-right"><i class="fal fa-user-circle mr-2"></i> My Account</a><br><a href="/logout" class="text-danger float-right"><small><i class="fal fa-sign-out mr-2"></i> Logout </small></a></div>');
					if ($('#active_service').val() == 'save_search') {
						add_alias();
					} else if ($('#active_service').val() == 'save_favorite') {
						add_favorite();
					}
				}
			}
		});

	}

	function login_user() {

		var email = $('#signin_email').val();
		var password = $('#signin_password').val();
		var remember_input = $('#signin_remember').val();
		var remember = false;
		if (remember_input == 'on') {
			remember = true;
		}
		if (email != '' && password != '') {
			$.ajax({
				type: 'post',
				url: '/login/user',
				data: {
					_token: _token,
					email: email,
					password: password,
					remember: remember
				},
				success: function (data) {
					if (data.status == 'error') {
						$('#signin_error').show();
					} else {
						$('#modalSignInForm').modal('hide');
						$('#nav_logged').html('<div id="nav_logged_in"><a href="/dashboard" class="mb-n2  float-right"><i class="fal fa-user-circle mr-2"></i> My Account</a><br><a href="/logout" class="text-danger float-right"><small><i class="fal fa-sign-out mr-2"></i> Logout </small></a></div>');
						if ($('#active_service').val() == 'save_search') {
							add_alias();
						} else if ($('#active_service').val() == 'save_favorite') {
							add_favorite();
						} else if ($('#active_service').val() == 'login') {
							if (!window.location.href.match(/listing_results/)) {
								window.location = '/dashboard';
							} else {
								toastr['success']('Successfully Logged In');
							}
						}
					}
				}
			});
		}
	}



	$('.open-register').click(function () {
		$('#modalRegisterForm').modal();
		$('#modalSignInForm').modal('hide');
		$('#active_service').val('register');
		$('#register_form').off().on('submit', function (e) {
			e.preventDefault();
			register_user();
		});
	});

	$('#signin_form').off().on('submit', function (e) {
		e.preventDefault();
		login_user();
	});

	$('.forgot-password').unbind('click').bind('click', function () {
		$('#resetPasswordModal').modal();
		$('#modalSignInForm').modal('hide');
		$('#modalRegisterForm').modal('hide');
		$('#reset_password_form').off().on('submit', function (e) {
			e.preventDefault();
			reset_password();
			$('#reset_password').val('Sending Email...');
		});
	});

	$('.open-login').click(function () {
		$('#modalSignInForm').modal();
		$('#modalRegisterForm').modal('hide');
		$('#active_service').val('login');
	});

	function reset_password() {

		var email = $('#reset_email').val();
		$.ajax({
			type: 'post',
			url: '/password/email',
			data: {
				email: email,
				_token: _token
			},
			success: function (response) {
				if (response.status == 'success') {
					$('#resetPasswordModal').modal('hide');
					toastr['success']('Password Reset Email Successfully Sent');
				} else {
					$('#reset_error').show();
				}
				$('#reset_password').val('Reset Password');
			}
		});
    }

    $('#contact_form').submit(function (e) {
        $('#contact_form_submit').html('Sending <i class="fas fa-spinner fa-spin"></i>').prop('disabled', true)
        e.preventDefault();
        $.ajax({
            type: 'POST',
            url: contact_route,
            data: { name: $('#name').val(), phone: $('#phone').val(), email: $('#email').val(), message: $('#message').val(), _token: $('[name="_token"]').val(), agent_id: $('#agent_id').val(), type: $('#type').val() },
            success: function (response) {
                toastr['success']('Your message was successfully sent!');
                $('#contact_form_submit').html('Send <i class="fal fa-share"></i>').prop('disabled', false);
                $('#contact_form').find('input, textarea').val('');
                $('label').removeClass('active');
                $('#email_agent_modal').modal('toggle');
            }
        });
    });


	// form submission validation
	(function () {
		'use strict';
       // window.addEventListener('load', function () {
			// Fetch all the forms we want to apply custom Bootstrap validation styles to
			var forms = document.getElementsByClassName('needs-validation');
			// Loop over them and prevent submission
			var validation = Array.prototype.filter.call(forms, function (form) {
				form.addEventListener('submit', function (event) {
					if (form.checkValidity() === false) {
						event.preventDefault();
						event.stopPropagation();
					}
					form.classList.add('was-validated');
				}, false);
			});
		//}, false);
	})();

});